.root {
  width: 720px;
  background: #1c1c21;
  border: 1px solid rgba(76, 76, 255, 0.2);
  box-sizing: border-box;
  box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04),
    0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04),
    0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
  position: relative;
}

.close {
  cursor: pointer;
  position: absolute;
  display: flex;
  top: 60px;
  right: 40px;
}

.header {
  height: 64px;
  display: flex;
  align-items: center;
}
